  const handleSave = (loadGrid, idPrefix, doClose) => {

    if(idPrefix === undefined) idPrefix = "";

    var json = {};
      
    if(document.getElementById("server_response_headers_header_id" + idPrefix).value.endsWith(":") !== true) {
      json["server_response_headers_header"] = document.getElementById("server_response_headers_header_id" + idPrefix).value;
    }
    if(document.getElementById("server_response_headers_macro_id" + idPrefix).value.endsWith(":") !== true) {
      json["server_response_headers_macro"] = document.getElementById("server_response_headers_macro_id" + idPrefix).value;
    }
    if(document.getElementById("server_response_headers_attribute_id" + idPrefix).value.endsWith(":") !== true) {
      json["server_response_headers_attribute"] = document.getElementById("server_response_headers_attribute_id" + idPrefix).value;
    }
    if(document.getElementById("server_response_headers_text_id" + idPrefix).value.endsWith(":") !== true) {
      json["server_response_headers_text"] = document.getElementById("server_response_headers_text_id" + idPrefix).value;
    }
      
    loadGrid( json["server_response_headers_header"], json["server_response_headers_macro"], json["server_response_headers_attribute"], json["server_response_headers_text"], true); 

    handleClose(idPrefix, doClose);
  };

  const handleAdd = (idPrefix, doAdd, doClose) => {
    if(idPrefix === undefined) idPrefix = "";


    document.getElementById("server_response_headers_header_id").value = "";
    document.getElementById("server_response_headers_macro_id").value = "";
    document.getElementById("server_response_headers_attribute_id").value = "";
    document.getElementById("server_response_headers_text_id").value = "";

    if(doAdd) {
      doAdd();
    }

    handleClose(idPrefix,  doClose);
  };

  const handleClose = (idPrefix, doClose) => {
    updateStyle("server_div" + idPrefix, "display", "block");
    document.getElementById("addserver_response_headersPage" + idPrefix).style.display = "none";

      if(doClose) {
        doClose();
      }
  };

  const closeAdd = (idPrefix) => {
      if(idPrefix === undefined) idPrefix = "";

      updateStyle("server_response_headers_div" + idPrefix, "display", "block");
      document.getElementById("server_response_headers_tab" + idPrefix).style.display = "flex";

  };


